<!--首页-->
<!--created by qiuwei-->
<template>
  <div class="page-home">
    <!--背景-->
    <div class="top-bac"></div>
    <div class="top-bac--petal"></div>
    <div class="top-bac--petal3"></div>
    <!--轮播图-->
    <div class="part-poll" ref="refPoll">
      <swiper :options="poll.option" v-if="poll.data.length">
        <swiper-slide v-for="item in poll.data" :key="item.id">
          <div class="poll-content">
            <a :href="item.url" v-if="item.url">
              <img :src="$supImg(item.img, '700x355')" @load="pollImgLoad"/>
            </a>
            <a v-if="!item.url">
              <img :src="$supImg(item.img, '700x355')" @load="pollImgLoad"/>
            </a>
          </div>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
    <!--顶部标题-->
    <ul class="part-banner">
      <li>携明星断舍离</li>
      <li>集赞送闲置</li>
      <li>一起轻生活</li>
    </ul>
    <!--商品列表-->
    <p class="part-title">
      <i class="star-left"></i>明星好物<i class="star-right"></i>
    </p>
    <div class="part-list">
      <goodList :data="goodsData"/>
      <p v-if="!isLoading && goodsData.length === 0">当前时段没有活动哦～</p>
    </div>
    <p class="part-go">
      <span @click="goHistory">查看往期精彩</span>
    </p>
    <!--推荐-->
    <p class="part-title">
      <i class="star-left"></i>赞助推荐<i class="star-right"></i>
    </p>
    <div class="part-recommend">
      <recommend :recommendKey="recommendKey"/>
    </div>
    <!--底部信息-->
    <p class="part-bottom">
      <span>本次活动解释权属京东旗下拍拍所有</span>
    </p>
    <!--我的-->
    <div class="mine" @click="goMine"></div>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import { cornerShare, pageWxShare, logSend } from '../../assets/js/util'
import { mapState, mapActions } from 'vuex'
import goodList from './goods-list'
import recommend from './recommend'

export default {
  name: 'page-home',
  components: { swiper, swiperSlide, goodList, recommend },
  data () {
    return {
      isLoading: true, // 商品列表是否正在加载
      recommendKey: '' // 推荐加载key
    }
  },
  computed: {
    ...mapState('home', [
      'pollData', // 轮播图数据
      'goodsData' // 商品列表
    ]),
    // 轮播图
    poll () {
      return {
        data: this.pollData,
        option: {
          speed: 800,
          autoplay: { delay: 3000, disableOnInteraction: false },
          loop: true,
          pagination: { el: '.swiper-pagination' }
        }
      }
    }
  },
  created () {
    document.body.style.background = 'transparent'
    // pv上报
    setTimeout(() => {
      logSend({
        page_title: '明星闲置首页'
      })
    }, 500)
  },
  async mounted () {
    // 拍拍App显示右上角分享
    cornerShare()
    // 获取轮播图数据
    this.getPollData()
    // 获取商品列表数据
    await this.getGoodsData()
    this.isLoading = false
    // 获取推荐信息
    this.recommendKey = 'other'
    // 微信分享图文控制
    pageWxShare()
  },
  methods: {
    ...mapActions('common', [
      'judgeJdIn' // 判断用户是否已京东登录 没有则跳登录页
    ]),
    ...mapActions('home', [
      'getPollData', // 获取轮播图数据
      'getGoodsData' // 获取商品列表数据
    ]),
    // 查看往期精彩
    goHistory () {
      this.$router.push(`/history`)
    },
    // 我的活动
    async goMine () {
      if (await this.judgeJdIn({url: `${location.protocol}//${location.host}/c2c/star/mine`})) {
        this.$router.push(`/mine`)
      }
    },
    // 轮播图片加载后让背景面板透明 防止白底
    pollImgLoad () {
      this.$refs.refPoll.style.background = 'none'
    }
  }
}
</script>

<style type="text/scss" rel="stylesheet/scss" lang="scss">
  .page-home {
    position: relative;
    overflow: hidden;
    min-height: 1350px;
    background: #ff5f7d;
    /*顶部半圆背景*/
    .top-bac {
      position: absolute;
      top: 0;
      left: -25%;
      width: 150%;
      height: 320px;
      background: linear-gradient(#FF3566 0%, #DE123A 100%);
      border-radius: 0 0 40% 40%;
    }
    .top-bac--petal {
      position: absolute;
      top: 450px;
      left: 160px;
      opacity: .7;
      width: 700px;
      height: 700px;
      background: url(../../assets/img/petal1.svg) no-repeat center;
      background-size: cover;
    }
    .top-bac--petal3 {
      position: absolute;
      top: 950px;
      left: -230px;
      opacity: .3;
      width: 700px;
      height: 700px;
      background: url(../../assets/img/petal3.svg) no-repeat center;
      background-size: cover;
    }
    /*轮播模块*/
    .part-poll {
      position: relative;
      width: 700px;
      height: 355px;
      margin: 24px auto;
      background: linear-gradient(-135deg, #FFF3F5 0%, #FFEFF2 100%);
      box-shadow: 0 20px 30px 0 rgba(255, 46, 85, .5);
      border-radius: 8px;
      overflow: hidden;
      .swiper-container {
        border-radius: 8px;
        overflow: hidden;
      }
      .poll-content {
        height: 355px;
        background: linear-gradient(-135deg, #FFF3F5 0%, #FFEFF2 100%);
        a {
          display: block;
          width: 100%;
          height: 100%;
          img {
            width: 100%;
            height: 100%;
            border-radius: 8px;
          }
        }
      }
      .swiper-slide {
        border-radius: 8px;
      }
      .swiper-pagination {
        text-align: right;
        padding: 0 20px 10px 0;
        .swiper-pagination-bullet {
          background: #fff;
          opacity: .5;
          border-radius: 50%;
        }
        .swiper-pagination-bullet-active {
          background: #fff;
          opacity: 1;
        }
      }
    }
    /*顶部标题*/
    .part-banner {
      display: flex;
      font-size: 22px;
      color: #fff;
      margin-bottom: 10px;
      li {
        position: relative;
        flex: 1;
        text-align: center;
        &:nth-child(-n+2):after {
          content: '';
          position: absolute;
          top: 50%;
          right: 0;
          width: 2px;
          height: 12px;
          margin-top: -6px;
          background: #fff;
          opacity: .8;
        }
        &:before {
          content: '';
          display: inline-block;
          width: 20px;
          height: 20px;
          background: url(../../assets/img/check@3x.png) no-repeat center;
          background-size: auto 100%;
          margin-right: 10px;
          vertical-align: -2px;
        }
      }
    }
    /*模块分隔标题*/
    .part-title {
      position: relative;
      font-size: 32px;
      color: #fff;
      text-align: center;
      padding: 30px;
      font-weight: bolder;
      .star-left, .star-right {
        display: inline-block;
        width: 75px;
        height: 28px;
        background: url(../../assets/img/star@3x.png) no-repeat center;
        background-size: auto 100%;
        vertical-align: -3px;
        margin: 0 10px;
      }
      .star-left {
        transform: rotate(180deg);
      }
    }
    /*商品列表模块*/
    .part-list {
      position: relative;
      z-index: 1;
      width: 700px;
      margin: 0 auto;
      & > p {
        color: #fff;
        font-size: 24px;
        text-align: center;
        padding: 30px 0;
      }
    }
    /*查看往期精彩*/
    .part-go {
      position: relative;
      z-index: 1;
      text-align: center;
      margin: 40px 0 28px 0;
      span {
        font-size: 28px;
        font-weight: 300;
        border: 1PX solid #FFFFFF;
        border-radius: 36px;
        padding: 10px 30px;
        color: #fff;
      }
    }
    /*推荐列表模块*/
    .part-recommend {
      position: relative;
      z-index: 1;
      width: 100%;
    }
    /*底部信息*/
    .part-bottom {
      position: relative;
      font-size: 24px;
      color: #9f0022;
      margin: 50px 0;
      text-align: center;
      opacity: .5;
      & > span {
        position: relative;
        &:before, &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: -65px;
          height: 1PX;
          width: 50px;
          background: linear-gradient(to right, transparent 0%, #9F0022 100%);
        }
        &:after {
          left: auto;
          right: -65px;
          background: linear-gradient(to left, transparent 0%, #9F0022 100%);
        }
      }
    }
    /*我的*/
    .mine {
      transition: all ease .5s;
      width: 100px;
      height: 100px;
      position: fixed;
      bottom: 32px;
      right: 32px;
      background: rgba(0, 0, 0, .8);
      border-radius: 50%;
      z-index: 9;
      text-align: center;
      padding-top: 23px;
      box-shadow: 0 10px 20px rgba(0, 0, 0, .2);
      &:before {
        content: '';
        position: absolute;
        top: -20%;
        left: -20%;
        width: 140%;
        height: 140%;
      }
      &:after {
        content: '';
        display: inline-block;
        width: 55px;
        height: 55px;
        background: url(../../assets/img/mine1@3x.png) no-repeat center;
        background-size: auto 100%;
      }
    }
  }
  /*安卓下一些特殊处理*/
  .underAndroid {
    .page-home {
      .part-go span {
        padding-top: 15px;
      }
    }
  }
</style>



